export default {
  methods: {
    checkAndHandleCompression(file) {
      return new Promise(resolve => {
        this.imgBase64(file, (image, canvas) => {
          let maxSize = 15 * 1024 * 1024; // 2M (單位KB)
          let fileSize = file.size; // 圖片大小 (單位KB)
          let uploadSrc, uploadFile;
          if (fileSize > maxSize) {
            // 如果圖片大小大於maxSize，進行壓縮
            uploadSrc = canvas.toDataURL(file.type, maxSize / fileSize);
            uploadFile = this.convertBase64UrlToFile(uploadSrc, file.name); // 轉成file檔案
          } else {
            uploadSrc = image.src;
            uploadFile = file;
          }
          let compressedSize = uploadFile.size / 1024; // 壓縮後圖片大小 (單位KB)
          // 判斷圖片大小是否小於maxSize，如果大於則繼續壓縮至小於為止
          if (compressedSize.toFixed(2) > maxSize) {
            this.checkAndHandleUpload(uploadFile);
          } else {
            let fileOptions = { uploadSrc, uploadFile };
            resolve(fileOptions);
          }
        });
      });
    },
    /**
     * 將圖片轉化為base64
     */
    imgBase64(file, callback) {
      // 看支援不支援FileReader
      if (!file || !window.FileReader) return;
      // 建立一個 Image 物件
      let image = new Image();
      // 繫結 load 事件處理器，載入完成後執行
      image.onload = function() {
        // 建立 canvas DOM 物件
        let canvas = document.createElement('canvas');
        // 返回一個用於在畫布上繪圖的環境, '2d' 指定了您想要在畫布上繪製的型別
        let ctx = canvas.getContext('2d');
        // 如果高度超標 // 引數，最大高度
        let MAX_HEIGHT = 3000;
        if (image.height > MAX_HEIGHT) {
          // 寬度等比例縮放 *=
          image.width *= MAX_HEIGHT / image.height;
          image.height = MAX_HEIGHT;
        }
        // 獲取 canvas的 2d 環境物件,
        // 可以理解Context是管理員，canvas是房子
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        // 重置canvas寬高
        canvas.width = image.width;
        canvas.height = image.height;
        // 將影象繪製到canvas上
        ctx.drawImage(image, 0, 0, image.width, image.height);
        callback(image, canvas);
      };
      if (/^image/.test(file.type)) {
        // 建立一個reader
        let reader = new FileReader();
        // 將圖片將轉成 base64 格式
        reader.readAsDataURL(file);
        // 讀取成功後的回撥
        reader.onload = function() {
          // 設定src屬性，瀏覽器會自動載入。
          // 記住必須先繫結事件，才能設定src屬性，否則會出同步問題。
          image.src = this.result;
        };
      }
    },
    /**
     * 把Base64轉換成file檔案
     */
    convertBase64UrlToFile(dataurl, filename) {
      let arr = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    }
  }
};
